body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  width: 50%;
  margin: auto;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.clearfix:before, .clearfix:after {
  content: '';
  display: table;
}

.clearfix:after {
  clear: both;
}

.clearfix {
  zoom: 1;
}

@media screen and (max-width:768px) {
  .App {
    width: 80%;
  }
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.input {
    margin-right: 20px;
    width: 100%;
    text-overflow: hidden;
    padding: 20px;
    font-size: 20px;
    text-align: center;
}
.header {
    background: #B3A08D;
    padding: 20px 10px 0px 10px;
    text-align: center;
    margin-bottom: 10px;
}

.header h1 {
    color: #F3F0F0;
}

.search-box {
    float: right;
    vertical-align: middle;
}

.search-img {
    width: 40px;
    height: 40px;
}

.sort-box {
    color: #F3F0F0;
    float: left;
    vertical-align: middle;
}

.sort-box label {
    padding: 5px;
}

.sort-items  {
    padding: 5px;
   font-size: 14px;
}


.to-do-input {
    background: white;
    border: none;
}

.strike-through {
    text-decoration: line-through;
}

.to-do p {
    width: 100%;
    overflow: hidden;
    color: #F3F0F0;
    display: block;
}

.todo-item {
    width: 65%;
    text-align: center;
    vertical-align: middle;
    display: inline-block;
}

.todo-item-input {
    padding: 5px;
    border: none;
    text-align: center;
    font-size: 20px;
    width: 100%;
}

.font-large {
    font-size: 20px;
    font-style: normal;
}

.button {
    margin-left: 10px;
    border-radius: 50%;
    height: 50px;
    width: 50px;
    font-size: 12px;
    border: 0;
}

.button img {
    width: 70%;
    height: 70%;
}

.checkbox {
	width: 20px;
    height: 20px;
}

.to-do {
    width: 100%;
    background: #B3A08D;
    border-top: 1px solid #D7D7D7;
    border-bottom: 1px solid #D7D7D7;
    padding: 20px;
    margin-top: 20px;
}

.todo-checkbox {
    width: 10%;
    vertical-align: middle;
    display: inline-block;
}

.todo-buttons {
    width: 25%;
    vertical-align: middle;
    display: inline-block;
}

@media screen and (max-width:1040px) {
    .button {
        height: 40px;
      width: 40px;
    }

    .button img {
        width: 100%;
        height: 100%;
    }
  }

  @media screen and (max-width:880px) {
   .todo-item {
       width: 55%;
   }

   .todo-buttons {
       width: 35%;
   }
  }

  @media screen and (max-width:450px) {
    .button {
        height: 25px;
      width: 25px;
    }
  }






